* {
    box-sizing: border-box;
}

html {
    font-family: Circular-Book, sans-serif;
    color: $clr3;
}

body {
    overflow: hidden;
    margin: 0;
    background-color: $clr1;
}

h1,h2,h3,h4,h5,h6 {
    margin:0;
}

.root{
    height: 100 vh;
}

a{
    text-decoration: none;
    color: $clr3;
}

ul {
    list-style-type: none;
}

p {
    margin: 0;
}

input, select{
    background-color: rgba($color: $clr3, $alpha: 0.1);
    border: 0;
    border-radius: 4px;
    outline: none;
    color: $clr3;
}
