// Flex 
.flex {
    display: flex;
}
.flex-col {
    display: flex;
    flex-direction: column;
}
.inline-flex {
    display: inline-flex;
}

// List 
.clean-list {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

// Text 
.vertical-text {
    writing-mode: tb-rl;
    transform: rotate(-180deg);  
}
.hidden {
    display: none;
}

.relative {
    position: relative;
}

.opacity-zero {
    opacity: 0;
}

